.go-home-btn{
    position: fixed;
    bottom: 28px;
    right: 28px;
    height: 60px;
    width: 60px;
    border-radius: 30px;
    border-style: none;
    box-shadow: 0px 4px 31px rgba(0,0,0,0.15);
}

.go-home-btn:hover{
    cursor: pointer;
    box-shadow: 1px 10px 32px rgba(0,0,0,0.45);
    transition: all 0.3s ease-in-out;
}

.white-bkg{
    background: #fff;
}

.gradient-bkg{
    background: linear-gradient(39.97deg, #03d6f3 0.79% , #2C3539 79.29%);;
}

.home-icon{
    width: 70%;
}